<template>
	<div class="home-component">
		<div class="section" id="section1">
			<div class="content">
				<SectionOne :animation="newClass[index]" :index="index" />
			</div>
		</div>
		<div class="section" id="section2">
			<div class="content"></div>
		</div>
		<div class="section" id="section3">
			<div class="content">
				<SectionThree />
			</div>
		</div>
		<div class="section" id="section4">
			<div class="content">
				<SectionFour :animation="newClass[index]" />
			</div>
		</div>
		<div class="section" id="section5">
			<div class="content">
				<SectionFive />
			</div>
		</div>
		<div class="section" id="section6">
			<div class="content">
				<SectionSix />
			</div>
		</div>
		<div class="section" id="section7">
			<div class="content">
				<SectionSeven />
			</div>
		</div>
		<div class="section" :class="newClass[index]" id="section8">
			<div class="content">
				<SectionEight />
			</div>
		</div>
		<div class="section" id="section9">
			<div class="content">
				<SectionNine />
			</div>
		</div>
		<div class="section" :class="newClass[index]" id="section10">
			<div class="content">
				<SectionTen />
				<!-- <Footer /> -->
			</div>
		</div>

		<!-- <Footer /> -->
	</div>
</template>

<script>
import SectionOne from "../components/home/SectionOne.vue";
import SectionThree from "../components/home/SectionThree.vue";
import SectionFour from "../components/home/SectionFour.vue";
import SectionFive from "../components/home/SectionFive.vue";
import SectionSix from "../components/home/SectionSix.vue";
import SectionSeven from "../components/home/SectionSeven.vue";
import SectionEight from "../components/home/SectionEight.vue";
import SectionNine from "../components/home/SectionNine.vue";
import SectionTen from "../components/home/SectionTen.vue";

// import Footer from "../components/includes/Footer.vue";

export default {
	name: "Home",
	components: {
		SectionOne,
		SectionThree,
		SectionFour,
		SectionFive,
		SectionSix,
		SectionSeven,
		SectionEight,
		SectionNine,
		SectionTen,
		// Footer,
	},
	data() {
		return {
			index: 0,
			newClass: {
				1: "animation1",
				2: "animation2",
				3: "animation3",
				4: "animation4",
				5: "animation5",
				6: "animation6",
				7: "animation7",
				8: "animation8",
				9: "animation9",
				10: "animation10",
				11: "animation11",
			},
		};
	},
	methods: {
		scrollTrigger() {
			const observer = new IntersectionObserver(
				(entries) => {
					let body = document.querySelector("#app");

					entries.forEach((entry) => {
						for (var i = 1; i <= Object.keys(this.newClass).length; i++) {
							if (entry.target.id == "section" + i) {
								entry.isIntersecting ? (this.index = i) : "";
							}
						}
						if (entry.target.id == "section7") {
							entry.isIntersecting
								? (body.style.backgroundColor = "#66ffb3")
								: "";
						} else if (
							entry.target.id == "section8" ||
							entry.target.id == "section9"
						) {
							entry.isIntersecting
								? (body.style.backgroundColor = "#ff99ff")
								: "";
						} else {
							entry.isIntersecting
								? (body.style.backgroundColor = "unset")
								: "";
						}

						// if (entry.target.id == "footer") {
						// 	entry.isIntersecting ? (this.index = 11) : "";
						// }
					});
				},
				{
					threshold: 0.1,
				}
			);
			const allSections = document.querySelectorAll(".section");
			allSections.forEach((section) => {
				observer.observe(section);
			});
			// const footer = document.querySelector("footer");

			// observer.observe(footer);
		},
	},
	mounted() {
		this.scrollTrigger();
	},
};
</script>

<style lang="scss"></style>
